import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { 
  Box, 
  VStack, 
  Heading, 
  Input, 
  Select, 
  Text, 
  Spinner,
  useToast 
} from '@chakra-ui/react';

function App() {
  const [frequencies, setFrequencies] = useState([]); // state
  const [selectedFrequency, setSelectedFrequency] = useState(''); // 
  const [dateTime, setDateTime] = useState('');
  const [loading, setLoading] = useState(false);
  const [audioUrl, setAudioUrl] = useState(null);
  const toast = useToast();

  //  API
  useEffect(() => {
    const fetchFrequencies = async () => {
      try {
        const response = await axios.get('http://127.0.0.1:5000/api/frequencies');
        const freqList = response.data.frequencies;
        setFrequencies(freqList);
        // 
        if (freqList.length > 0) {
          setSelectedFrequency(freqList[0]);
        }
      } catch (error) {
        console.error('Failed to fetch frequencies:', error);
        toast({
          title: '',
          description: '',
          status: 'error',
          duration: 3000,
          isClosable: true,
        });
      }
    };
    fetchFrequencies();
  }, [toast]);

  //  selectedFrequencydateTime
  useEffect(() => {
    if (!dateTime || !selectedFrequency) {
      return;
    }
    
    const searchRecording = async () => {
      setLoading(true);
      setAudioUrl(null);
    
      const formattedDate = dateTime.replace(/:/g, '-');
      
      try {
        const response = await axios.get('http://127.0.0.1:5000/api/search', {
          params: {
            frequency: selectedFrequency,
            date: formattedDate,
          },
        });
        
        if (response.data.success) {
          setAudioUrl(`http://127.0.0.1:5000${response.data.url}`);
          toast({
            title: '',
            description: '',
            status: 'success',
            duration: 3000,
            isClosable: true,
          });
        } else {
          toast({
            title: '',
            description: response.data.error,
            status: 'warning',
            duration: 3000,
            isClosable: true,
          });
        }
      } catch (error) {
        console.error('API Error:', error);
        toast({
          title: 'API',
          description: '',
          status: 'error',
          duration: 3000,
          isClosable: true,
        });
      } finally {
        setLoading(false);
      }
    };
    
    searchRecording();
  }, [dateTime, selectedFrequency, toast]);


  return (
    <Box p={8} bg="gray.50" minH="100vh">
      <VStack spacing={8} maxW="container.md" mx="auto" pt={16}>
        <Heading as="h1" size="xl">
          wave-radio-cast
        </Heading>
        <Text fontSize="lg" color="gray.600">
          FM
        </Text>
        <VStack spacing={4} w="full">
          <Select value={selectedFrequency} onChange={(e) => setSelectedFrequency(e.target.value)}>
            {frequencies.length > 0 ? (
              frequencies.map((freq) => (
                <option key={freq} value={freq}>
                  {freq}
                </option>
              ))
            ) : (
              <option value="">...</option>
            )}
          </Select>
          <Input 
            type="datetime-local" 
            value={dateTime} 
            onChange={(e) => setDateTime(e.target.value)} 
            step="60"
          />
        </VStack>
        {loading ? (
          <Spinner size="xl" color="blue.500" />
        ) : audioUrl && (
          <Box mt={8} w="full">
            <Text mb={2} fontWeight="bold">...</Text>
            <audio controls autoPlay src={audioUrl} style={{ width: '100%' }}></audio>
          </Box>
        )}
      </VStack>
    </Box>
  );
}

export default App;